import dispatcher from '../dispatcher' 

// Flux      ,      . 
//    ,         , 
//           .

export function count() {
    dispatcher.dispatch({
        type: 'COUNT'
    })
} 


export function changeStyle() {
    dispatcher.dispatch({
        type: 'CHANGE_STYLE' 
    })
} 

//    view  action,  action  store  
export function showText(text) {
    dispatcher.dispatch({
        type: 'SHOW_TEXT', 
        text
    })
}


